.inventorybtn {
  width: 120px;
  height: 120px;
  margin: 5px;
  border-radius: 5px;
  box-shadow: inset 0 0 5px black;  
  background-color: green;  
  position: relative;
  display: inline-block;
  overflow: hidden;
}

.inventorybtn:after {
  content: "";
  position: absolute;
  top: -50px;
  left: -50px;
  width: 0%;
  height: 200%;
  transform: rotate(30deg);
  background-color: rgba(255,255,255,0.4);
  transition: none;
}

.inventorybtn:hover:after {
  width: 200%;
  background-color: rgba(255,255,255,0);
  transition: all 0.5s ease-out;
}

.moveInLR {
  animation: moveInLR 0.5s linear 0s 1 normal none;
}

.moveInRL {
  animation: moveInRL 0.5s linear 0s 1 normal none;
}

@keyframes moveInLR {
  from {left: -100%;}
  to { left : 0}
}


@keyframes moveInRL {
  from {left: 100%;}
  to { left : 0}
}


.blink {
    animation: blink 0.4s ease-in-out 0s infinite alternate none;
}

.blinkmomentary {
  animation: blink 0.1s ease 0s 4 normal none;
}


.blinkContinue {
  animation: blink 0.5s ease-in-out 1.5s infinite alternate forwards;
}



@keyframes blink {
  from { opacity: 0; }
  to   { opacity : 1; }
}

.battleIntroRL {
    animation: battleIntroRLEnter 0.5s ease-out 0s 1 normal forwards,
                battleIntroRLLeave 0.5s ease-out 1.5s 1 normal forwards;
}

@keyframes battleIntroRLEnter {
  from { left: 100%; }
  to { left : 75%; }
}

@keyframes battleIntroRLLeave {
  from { left: 75%; }
  to { left : -100%; }
}

.battleIntroLR {
  animation: battleIntroLREnter 0.5s ease-out 0s 1 normal forwards,
              battleIntroLRLeave 0.5s ease-out 1.5s 1 normal forwards;
}

@keyframes battleIntroLREnter {
from { left: 0%; }
to { left : 25%; }
}

@keyframes battleIntroLRLeave {
from { left: 25%; }
to { left : 100%; }
}

.battleIntroVS {
  animation: battleVS 0.5s ease-in 0s 1 normal forwards,
              battleVS 0.5s ease-out 1.5s 1 reverse forwards ;
}

@keyframes battleVS {
from {
    color: black;
    transform: scale(0);
    }
to { 
    color: red;
    transform: scale(2.0);
}
}
  
.wonBlink {
  animation: wonBlink 0.2s ease-in-out 0.5s 4 alternate forwards;
}

@keyframes wonBlink {
  from { color: forestgreen;}
  to { color: lawngreen	;}
}

.lostBlink {
  animation: lostBlink 0.2s ease-in-out 0.5s 4 alternate forwards;
}

@keyframes lostBlink {
  from { color: maroon;}
  to { color: crimson	;}
}

.rotate {
  animation: rotate 1.5s linear 0s infinite normal none;
}

@keyframes rotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.selected {
 border: 10px #57ABB3 solid;
 border-radius: 5px;
}

a {color:black; text-decoration: none;} 
a:disabled {color:black;}        /* Unvisited link  */
a:visited {color:black;} /* Visited link    */
a:hover {color:white;}   /* Mouse over link */
a:active {color:lightseagreen;}  /* Selected link   */

button { color: black; }
button:hover { color: white; }
button:disabled { color: lightgray; }


.spotanimate {
  animation: spotanimate 0.8s steps(4) 0s infinite normal forwards;
}

@keyframes spotanimate {  
  0% { background-position: 0px 0px }
  100% { background-position: 0px 144px }
}


.shine {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0.4;
  animation: shine 2s linear 0s infinite normal none;
  transform: rotate(-45deg);
  background-color: #b61d20;

}

@keyframes shine {
  0% { 
       width: 0%;
       height: 0%;
       
       }
  100% {
      width: 300%;
      height: 200%;
  }
}

.animateTitle {
  animation: moveTitle 1s ease-in-out 0s 1 normal forwards;
}

@keyframes moveTitle {
  from {
    top: -100%;
   
  }
  to {
    top: 0%;
  }
}


.showMenu {
  animation: blink 0.3s linear 1s 1 normal forwards;
}

.wind {
  animation: wind 0.5s linear 0s infinite alternate forwards;
}

@keyframes wind {
  from {
    transform: translate(-50%, -50%);    
  }
  to {
    transform: translate(-50%, -40%);
  }
}

